form {
  position: relative;
  width: 300px;
  margin: 0 auto;
}

.d1 {
  padding: 30px 0;
  background: #a3d0c3;
}

.d1 input {
  width: 100%;
  height: 42px;
  padding-left: 10px;
  border: 2px solid #7ba7ab;
  border-radius: 5px;
  outline: none;
  color: #1d1d1d;
}

.d1 button {
  position: absolute;
  top: 0;
  right: 0px;
  width: 42px;
  height: 42px;
  border: none;
  background: #7ba7ab;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
}

.d1 button:before {
  content: '\f002';
  font-family: FontAwesome;
  font-size: 16px;
  color: #fff;
}
